.databox table,
.databox tr,
.databox td,
.databox th {
  border: 1px solid #000;
}
.databox table {
  width: 100%;
  font-size: 20px;
}
.databox table tr:nth-child(2n) {
  background-color: lightblue;
}
.databox .settablebox {
  background-color: rgba(0, 0, 0, 0.6);
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 9999;
}
.databox .innerbox {
  width: 500px;
  height: 200px;
  background-color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -250px;
  margin-top: -100px;
  display: none;
  padding: 10px;
}
.databox .innerbox .closebtn {
  position: absolute;
  right: -10px;
  top: -10px;
  width: 20px;
  height: 20px;
  background-color: orange;
  color: white;
  border-radius: 50%;
  text-align: center;
  font-size: 20px;
  text-decoration: none ;
  line-height: 20px;
}
.databox .innerbox label {
  padding: 0 20px;
}
