* {
  margin: 0;
  padding: 0;
}
.range {
  padding: 16px;
  padding-bottom: 30px;
  border: 1px solid #000;
  position: relative;
  border-sizing: content-box;
  width: 460px;
}
.range .btn {
  position: absolute;
  top: 15px;
  left: 382px;
}
.range .bar {
  height: 16px;
  background-color: #9e9e9e;
  border-radius: 6px;
  position: relative;
}
.range .bar b {
  position: absolute;
  top: 0px;
  height: 20px;
  width: 10px;
  background-color: red;
  z-index: 66;
  cursor: pointer;
}
.range .bar b u {
  font-style: normal;
  font-size: 12px;
  position: absolute;
  width: 20px;
  height: 10px;
  text-align: center;
  left: -10px;
  top: -10px;
  /*background-color:red;*/
  text-decoration: none;
}
.range .bar span {
  position: absolute;
  top: 0;
  height: 16px;
  background-color: #03A9F4;
}
.range .scaleline {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 28px;
  padding: 0 16px;
  box-sizing: border-box;
  /*background-color:orange;*/
}
.range .scaleline i {
  float: left;
  width: 1px;
  height: 8px;
  background-color: orange;
}
.range .scaleline i.big {
  height: 14px;
  position: relative;
}
.range .scaleline i u {
  font-style: normal;
  font-size: 12px;
  position: absolute;
  width: 20px;
  height: 10px;
  text-align: center;
  left: -10px;
  top: 10px;
  /*background-color:red;*/
  text-decoration: none;
}
