.filterbar {
  margin-top: 20px;
}
.filterbar a {
  margin-right: 20px;
  padding: 4px 6px;
}
.filterbar a.cur {
  border: 1px solid orange;
}
.filterbar .mutibtn {
  border: 1px solid #333;
}
.filterbar label {
  margin-right: 20px;
}
.filterbar .radio,
.filterbar .checkbox {
  margin-top: 0;
}
.filter_t {
  line-height: 50px;
}
.nowfilter {
  overflow: hidden;
}
.nowfilter ul {
  list-style: none;
  overflow: hidden;
  padding-left: 0;
}
.nowfilter ul li {
  float: left;
  padding: 4px 10px;
  border: 1px solid red;
  margin-right: 10px;
  padding-right: 24px;
  position: relative;
  cursor: pointer;
}
.nowfilter ul li.t {
  border: none;
}
.nowfilter ul li::after {
  content: "×";
  position: absolute;
  width: 16px;
  height: 16px;
  right: 0;
  top: 4px;
  font-size: 18px;
  line-height: 20px;
}
.nowfilter ul li.t::after {
  content: none;
}
