.BECanlendar {
  margin: 10px;
  width: 300px;
  position: relative;
  font-family: "微软雅黑";
}
.BECanlendar .result {
  width: 300px;
  height: 26px;
  border: 1px solid #BDBDBD;
  border-radius: 4px;
  text-align: left;
  line-height: 26px;
  padding-left: 10px;
  box-sizing: border-box;
  position: relative;
  cursor: pointer;
  font-size: 14px;
}
.BECanlendar .result span.canlendarbtn {
  position: absolute;
  right: 0;
  top: 0;
  width: 40px;
  height: 24px;
  border-left: 1px solid #BDBDBD;
}
.BECanlendar .result span.canlendarbtn::before {
  position: absolute;
  top: 5px;
  left: 11px;
  font-size: 16px;
  color: #424242;
}
.BECanlendar .chooseBox {
  width: 500px;
  border: 1px solid #BDBDBD;
  position: absolute;
  top: 38px;
  left: 0;
  border-radius: 4px;
  padding: 4px 8px;
  box-shadow: darkgrey 10px 10px 30px 5px ;
  box-sizing: border-box;
  z-index: 1000;
  padding-top: 50px;
  background-color: white;
}
.BECanlendar .chooseBox .cancelbtn {
  position: absolute;
  top: 4px;
  left: 4px;
}
.BECanlendar .chooseBox .submitbtn {
  position: absolute;
  top: 4px;
  right: 4px;
}
.BECanlendar .chooseBox .begin {
  float: left;
  width: 40%;
  position: relative;
}
.BECanlendar .chooseBox .days {
  float: left;
  width: 20%;
  text-align: center;
  line-height: 50px;
  font-size: 21px;
  color: orange;
}
.BECanlendar .chooseBox .end {
  float: left;
  width: 40%;
  position: relative;
}
.BECanlendar .chooseBox .result {
  width: 175px;
  height: 24px;
}
.BECanlendar .chooseBox .result span.canlendarbtn {
  font-size: 16px;
  height: 22px;
  width: 34px;
}
.BECanlendar .chooseBox .result span.canlendarbtn::before {
  position: absolute;
  top: 5px;
  left: 9px;
  font-size: 14px;
  color: #424242;
}
.canlendarChooser {
  position: absolute;
  top: 41px;
  left: 0;
  width: 265px;
  height: 245px;
  border: 1px solid #BDBDBD;
  background: white;
  box-shadow: 2px 2px 1px #433e3e;
  padding: 10px;
}
.canlendarChooser h4 {
  text-align: center;
  font-size: 16px;
  font-weight: bold;
}
.canlendarChooser .leftBtn {
  position: absolute;
  top: 22px;
  left: 11px;
  width: 16px;
  height: 16px;
  background: url(/images/ll.jpg);
  background-size: contain;
}
.canlendarChooser .rightBtn {
  position: absolute;
  top: 22px;
  right: 11px;
  width: 16px;
  height: 16px;
  background: url(/images/rr.jpg);
  background-size: contain;
}
.canlendarChooser th {
  text-align: center;
}
.canlendarChooser td {
  padding: 0px 10px;
  width: 14px;
  height: 16px;
  font-size: 12px;
  text-align: center;
  /*解决文字双击 被选中变色？*/
}
.canlendarChooser td.gray {
  color: #BDBDBD;
}
.canlendarChooser td.cur {
  background-color: orange;
  border-radius: 50%;
}
.canlendarChooser td:hover {
  background-color: lightblue;
}
.canlendarChooser .YearMonthPicker {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  top: 0;
  left: 0;
  z-index: 99;
}
.canlendarChooser .YearMonthPicker .inner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 230px;
  height: 200px;
  background-color: white;
  padding: 10px;
  box-sizing: border-box;
}
.canlendarChooser .YearMonthPicker .inner .year_panel {
  width: 100%;
  padding: 0 10px;
}
.canlendarChooser .YearMonthPicker .inner .year_panel .panel_inner {
  overflow: hidden;
  width: 100%;
}
.canlendarChooser .YearMonthPicker .inner .year_panel .panel_inner .span_container {
  position: relative;
  top: 0;
  left: 0px;
  width: 1000px;
}
.canlendarChooser .YearMonthPicker .inner .year_panel .panel_inner .span_container span {
  float: left;
  width: 38px;
  border: 1px solid #E0E0E0;
}
.canlendarChooser .YearMonthPicker .inner .year_panel .panel_inner .span_container span.cur {
  font-weight: bold;
}
.canlendarChooser .YearMonthPicker .inner .year_panel .ll {
  position: absolute;
  top: 14px;
  left: 1px;
  width: 16px;
  height: 16px;
  background: url(/images/ll.jpg);
  background-size: contain;
}
.canlendarChooser .YearMonthPicker .inner .year_panel .rr {
  position: absolute;
  top: 14px;
  right: 1px;
  width: 16px;
  height: 16px;
  background: url(/images/rr.jpg);
  background-size: contain;
}
.canlendarChooser .YearMonthPicker .inner .month_panel {
  width: 100%;
}
.canlendarChooser .YearMonthPicker .inner .month_panel .col {
  float: left;
  width: 50%;
}
.canlendarChooser .YearMonthPicker .inner .month_panel .col a {
  display: block;
  text-align: center;
}
.canlendarChooser .YearMonthPicker .inner .month_panel .col .cur {
  border: 1px solid #000;
  background-color: orange;
}
